import React, {useContext, useRef, useState} from "react";
import TabHeader from "./TabHeader";
import {GAME_INFO_TAB} from "../../../language/Vietnamese/GameInfo_vi";
import GameSettingTab from "./game-setting/GameSettingTab";
import {COLOR} from "../../shared/styled/ColorsPalette";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import MyRoleInfo from "./my-role-info/MyRoleInfo";
import RoomInfo from "./room-info/RoomInfo";
import {SizeContext} from "../../../context";

function GameInfoView() {
    const [tab, setTab] = useState<GAME_INFO_TAB>(GAME_INFO_TAB.MY_INFO);
    const tab_header = useRef<HTMLDivElement>(null);
    const {basic} = useContext(SizeContext);
    const tabHeight = basic.gameH - (tab_header.current?.clientHeight || 0);
    return (
        <GameLayout.ColExpanded $backgroundColor={COLOR.blurDark80} round={"1rem"}>
            <div ref={tab_header}>
                <TabHeader onTabChange={setTab} activeTab={tab} />
            </div>
            <GameLayout.ColExpanded>
                <>{tab === GAME_INFO_TAB.GAME_SETTING && <GameSettingTab height={tabHeight} />}</>
                <>{tab === GAME_INFO_TAB.MY_INFO && <MyRoleInfo />}</>
                <>{tab === GAME_INFO_TAB.VILLAGE_INFO && <RoomInfo />}</>
            </GameLayout.ColExpanded>
        </GameLayout.ColExpanded>
    );
}

export default GameInfoView;
