import React, {useContext} from "react";
import PropTypes from "prop-types";
import backBtn from "../../../assets/game-btn/back.svg";
import Grid from "../../shared/styled/game-layout/Grid";
import {COLOR} from "../../shared/styled/ColorsPalette";
import {useTranslation} from "react-i18next";
import {GAME_INFO_TAB, GAME_INFO_TABS, GameInfoEnum} from "../../../language/Vietnamese/GameInfo_vi";
import {GameText} from "../../shared/game-basic-components/GameText";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {GameRouterContext} from "../../shared/game-advance-components/GameRouter";
import {SizeContext} from "../../../context";
import GameIconButton from "../../shared/game-basic-components/GameIconButton";
import {POSITION} from "../../shared/styled/game-layout/StackLayout";
import {RoomContext} from "../../../context";
import {UserContext} from "../../../context";

TabHeader.propTypes = {
    onTabChange: PropTypes.func,
    activeTab: PropTypes.oneOf(GAME_INFO_TABS)
};

function TabHeader(props) {
    const {basic: baseSize} = useContext(SizeContext);
    const {t} = useTranslation("GameInfo");
    const router = useContext(GameRouterContext);
    const roomContext = useContext(RoomContext);
    const userContext = useContext(UserContext);
    const showVillageInfo = userContext.playerId === roomContext.state.roomSetting.roomAdmin;

    function goBack() {
        if (router?.routeTo) router.routeTo("StageRouter", []);
    }

    function changeTab(tab: GAME_INFO_TAB) {
        if (props.onTabChange) props.onTabChange(tab);
    }

    return (
        <GameLayout.Col mb={"1rem"}>
            <GameLayout.Row>
                <GameIconButton
                    $textPosition={POSITION.RIGHT}
                    color={COLOR.bloodColor}
                    onClick={goBack}
                    src={backBtn}
                    size={baseSize.size5}
                    mx={baseSize.iconButtonSize * 0.1}
                >
                    {t(`${GameInfoEnum.back}`)}
                </GameIconButton>
                <GameLayout.ColExpanded />
            </GameLayout.Row>

            <Grid.Row $justifyContent="space-around">
                {GAME_INFO_TABS.map((tab, index) => {
                    if (tab === GAME_INFO_TAB.VILLAGE_INFO && showVillageInfo) return <></>;
                    else
                        return (
                            <GameDiv
                                cursor={"pointer"}
                                key={"tab" + index}
                                style={{
                                    borderBottom: props.activeTab === tab ? "solid red 1px" : ""
                                }}
                                onClick={() => changeTab(tab)}
                            >
                                <GameText
                                    color={COLOR.bloodColor}
                                    size={baseSize.mobileView ? baseSize.size4 : baseSize.size6}
                                    $fontStyle={"Altus"}
                                >
                                    {t(`${GameInfoEnum.tabName}.${tab}`)}
                                </GameText>
                            </GameDiv>
                        );
                })}
            </Grid.Row>
        </GameLayout.Col>
    );
}

export default TabHeader;
