import React, {useContext, useEffect} from "react";
import {useTranslation} from "react-i18next";
import {GameText} from "../../../shared/game-basic-components/GameText";
import {GameDiv} from "../../../shared/game-basic-components/GameDiv";
import GameSlider from "../../../shared/game-advance-components/GameSlider";
import {SizeContext, StateContext} from "../../../../context";
import {I18Namspace} from "../../../../language/i18n";
import GameSettingView from "./GameSettingView";
import {GameSettingEnum} from "../../../../language/Vietnamese/GameSetting_vi";
import {
    IPreSetup,
    MAX_PLAYER_IN_GAME,
    MIN_PLAYER_IN_GAME,
    PRE_SETUPS
} from "../../../../werewolf-consts/definitions/PresetSetups";
import {Roles} from "../../../../werewolf-consts/enums";
import {MapSchema} from "@colyseus/schema";
import {arrayNumber2MapSchema, mapSchemaNumber2Array} from "../../../../werewolf-consts/colyseus/Utils";
import {calculateBalancePoint} from "../../../../werewolf-consts/utils/create_setup_tool/AutoGenerateSetupV2";
import GameLayout from "../../../shared/game-advance-components/GameLayout";

SettingPicker.propTypes = {};

interface IProps {
    activeSetup: MapSchema<number> | Roles[];
    onChangeSetup?: (setup: MapSchema<number>) => void;
    onEditSetup?: (setup: MapSchema<number>) => void;
}

function SettingPicker(props: IProps) {
    const setupArr = Array.isArray(props.activeSetup) ? props.activeSetup : mapSchemaNumber2Array(props.activeSetup);
    let activePresetup: IPreSetup = {
        setup: setupArr,
        balancePoint: calculateBalancePoint(setupArr)
    };
    useEffect(() => {}, [setupArr]);
    const {t} = useTranslation([I18Namspace.GameSetting, I18Namspace.RoleGuide]);
    const {basic: baseSize} = useContext(SizeContext);
    let state = useContext(StateContext);
    let playerCount = state.players.size;
    let showSetup = playerCount >= MIN_PLAYER_IN_GAME && playerCount <= MAX_PLAYER_IN_GAME;

    function pickSetting(setup: MapSchema<number>) {
        if (props.onChangeSetup) props.onChangeSetup(setup);
    }

    function editSetup(setup: MapSchema<number>) {
        if (props.onEditSetup) props.onEditSetup(setup);
    }

    return (
        <GameLayout.ColExpanded $alignSelf={"center"} $justifyContent={"flex-end"} my={"1rem"}>
            <>
                {!showSetup && (
                    <GameLayout.Row
                        border="solid white .2rem"
                        round={"1rem"}
                        $H={baseSize.gameH * 0.3}
                        m={"1rem"}
                        $justifyContent={"center"}
                        $alignItems={"center"}
                    >
                        <GameText size={baseSize.size8} $fontStyle={"Altus"}>
                            {t(`${I18Namspace.GameSetting}:${GameSettingEnum.noSetupFound}`, {
                                playerCount: playerCount
                            })}
                        </GameText>
                    </GameLayout.Row>
                )}
            </>
            <>
                {showSetup && (
                    <GameSlider>
                        {[activePresetup].concat(PRE_SETUPS[playerCount]).map((setup, index) => (
                            <GameSettingView
                                key={"gstw" + index}
                                activated={index === 0}
                                balancePoint={setup.balancePoint}
                                setup={arrayNumber2MapSchema(setup.setup)}
                                onCheckActive={(setup) => pickSetting(setup)}
                                onEdit={(setup) => editSetup(setup)}
                            />
                        ))}
                    </GameSlider>
                )}
            </>
        </GameLayout.ColExpanded>
    );
}

export default SettingPicker;
