import {useEffect, useState} from "react";
import {ISize} from "./useWindowSize";

export interface IBasicSize {
    mobileView: boolean;
    chatW: number;
    chatH: number;
    gameW: number;
    gameH: number;
    iconButtonSize: number;
    playerAvatarSize: number;
    dialogW: number;
    dialogH: number;
    size10: number;
    size9: number;
    size8: number;
    size7: number;
    size6: number;
    size5: number;
    size4: number;
    size3: number;
    size2: number;
    size1: number;
}

export const defaultSize = {
    mobileView: false,
    iconButtonSize: 50,
    chatH: 0,
    gameH: 0,
    chatW: 0,
    gameW: 100,
    playerAvatarSize: 50,
    dialogW: 0,
    dialogH: 0,
    size10: 100,
    size8: 80,
    size6: 60,
    size4: 40,
    size2: 20,
    size9: 90,
    size7: 70,
    size5: 50,
    size3: 30,
    size1: 10
} as IBasicSize;

function useBasicSize(S: ISize) {
    const [basicSize, setBasicSize] = useState<IBasicSize>(defaultSize);
    let mobileView = S.isVertical;
    useEffect(() => {
        // if (!S.isVertical)
        let iconButtonSize = Math.min(Math.max(40, S.min / 18), 90);
        let chatH = S.isVertical ? S.height * 0.9 : S.height;
        let chatW = S.isVertical ? S.width : S.width / 4;
        let gameH = S.height;
        let gameW = S.isVertical ? S.width : S.width - chatW * 1.1;
        let dialogH = Math.max(S.height / 2.3, 250);
        let dialogW = (dialogH * 4) / 5;
        let playerAvatarSize = Math.min(Math.max(gameW / 15, 50), 100);
        let font1 = Math.min(Math.max(gameW / 300, 5), 40);
        setBasicSize({
            mobileView: mobileView,
            iconButtonSize: iconButtonSize,
            chatH: chatH,
            chatW: chatW,
            gameH: gameH,
            gameW: gameW,
            playerAvatarSize: playerAvatarSize,
            dialogW: dialogW,
            dialogH: dialogH,
            size10: font1 * 10,
            size9: font1 * 9,
            size8: font1 * 8,
            size7: font1 * 7,
            size6: font1 * 6,
            size5: font1 * 5,
            size4: font1 * 4,
            size3: font1 * 3,
            size2: font1 * 2,
            size1: font1
        });
    }, [S.height, S.isVertical, S.min, S.width, mobileView]);
    return basicSize;
}

export default useBasicSize;
