import React, {useContext} from "react";
import useClock from "./Clock";
import {COLOR} from "../../shared/styled/ColorsPalette";
import AvatarView from "../../shared/game-basic-components/AvatarView";
import botAvatar from "../../../assets/game-btn/botAvatar.png";
import {RoomContext, SizeContext, StateContext, UserContext} from "../../../context";
import {GameDiv} from "../../shared/game-basic-components/GameDiv";
import {GameText} from "../../shared/game-basic-components/GameText";
import GameLayout from "../../shared/game-advance-components/GameLayout";
import {GameRouterContext} from "../../shared/game-advance-components/GameRouter";
import {useTranslation} from "react-i18next";
import {I18Namspace} from "../../../language/i18n";
import {POSITION} from "../../shared/styled/game-layout/StackLayout";
import logout from "../../../assets/game-btn/logout.svg";
import {GameInfoEnum} from "../../../language/Vietnamese/GameInfo_vi";
import GameIconButton from "../../shared/game-basic-components/GameIconButton";

function GameHeader(props) {
    let state = useContext(StateContext);
    let closeTime = useClock(state.currentStage.closeTime);
    const {t} = useTranslation([I18Namspace.StageDescription, I18Namspace.GameInfo]);
    const user = useContext(UserContext);
    let stageDescription = t(`${I18Namspace.StageDescription}:${state.currentStage.stageName}`, {
        name: user.playerName
    });
    const {basic} = useContext(SizeContext);
    let size = Math.max(50, basic.gameW / 15);
    const room = useContext(RoomContext).room;
    const router = useContext(GameRouterContext);

    function showRoomSetting() {
        if (router?.routeTo) {
            router.routeTo("GameInfoView", []);
        }
    }

    return (
        <GameLayout.Row px={".5rem"} pt={".5rem"} position={"relative"}>
            <GameIconButton
                $alignSelf={"flex-start"}
                onClick={() => {
                    if (router?.routeTo) router.routeTo("home", []);
                    if (room?.leave) room.leave();
                }}
                $textPosition={POSITION.RIGHT}
                src={logout}
                size={basic.size5}
            >
                {t(`${I18Namspace.GameInfo}:${GameInfoEnum.exit}`)}
            </GameIconButton>
            <GameLayout.ColExpanded $justifyContent={"flex-end"}>
                <GameDiv $alignSelf={"flex-end"} mt={"2%"} px={"2%"}>
                    <GameText
                        size={basic.mobileView ? basic.size3 : basic.size5}
                        $backgroundColor={COLOR.blue}
                        round={"1rem"}
                        p={".5rem"}
                        $fontStyle={"Altus"}
                    >
                        {stageDescription} {closeTime}
                    </GameText>
                </GameDiv>
            </GameLayout.ColExpanded>
            <AvatarView
                $backgroundColor={"black"}
                cursor={"pointer"}
                src={botAvatar}
                size={size}
                onClick={showRoomSetting}
            />
        </GameLayout.Row>
    );
}

export default GameHeader;
